.Information-list {
  height: 75px;

  &::after {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }

  .p-d-i-icon {
    display: inline-block;
    font-size: 32px;
    vertical-align: middle;
    margin-right: 10px;

    i {
      font-size: 32px;
    }
  }

  .num-text {
    display: inline-block;
    padding: 0 3px;
    vertical-align: middle;

    .text {
      margin: 0;
      height: 24px;
      font-size: 13px;
      //    
      font-weight: 400;
      color: rgba(1, 1, 1, 0.6);
      line-height: 24px;
    }

    .num {
      margin: 0;
      height: 24px;
      font-size: 16px;
      //    
      font-weight: 400;
      color: rgba(25, 25, 25, 1);
      line-height: 24px;
    }
  }
}